export default {
  data() {
    return {
        newsInfo:"",
        noticeInfo:""
    }
  },
  onLoad() {
    this.fetchNewData();
    this.fetchNoticeData();
  },
  methods: {
    fetchNewData(){
      var url = "news/getList";
      var data = {
        page:1,
        rows:2
      }
      this.$Request.get(url,data).then(res => {
        if(res && res.code == 200 && res.data.length !== 0){
          this.newsInfo = res.data;
          console.log(res);
        }
      })
    },
    fetchNoticeData(){
      var url = "notice/getList";
      var data = {
        page:1,
        row:10
      };
       this.$Request.get(url,data).then(res => {
        if(res && res.code == 200 && res.data.length !== 0){
          this.noticeInfo = res.data;
        }
      })
    }
  }
}
